export function getFilterData(params = {}){
	return new Promise((resolve , reject) => {
		// mock
		// resolve(filter) ;  return ;
		get(API , Object.assign({action : filterAction} , disablejob , params))
		.then((data) => { resolve(data) })
		.catch(e => reject(e)) ;
	}) ;
}

export function getWorklistData(params = {} , options = {}){
	return new Promise((resolve , reject) => {
		// resolve(list) ; return ;
		get(API , Object.assign({action : worklistAction} , disablejob , params , options))
		.then((data) => { resolve(data) })
		.catch(e => reject(e)) ;
	}) ;
}
// http://m.58.com/sublocals/?cityname=bj&callback=jsonp2&_=1526005712408
export async function getLocation(cityname = 'bj'){
	let url = `/sublocals/?cityname=${cityname}`;
	return new Promise((resolve , reject) => {
		myjsonp(url)
		.then((data) => { resolve(data); })
		.catch(e => reject(e)) ;
	}) ;
}
// http://m.58.com/api/thirdcate/bj/yewu/?callback=jsonp3&_=1526005712413
export async function getPosition(cityname = 'bj'){
	let url = `/api/thirdcate/${cityname}/yewu/` ;
	return new Promise((resolve , reject) => {
		myjsonp(url)
		.then((data) => { resolve(data) ;})
		.catch(e => reject(e)) ;
	}) ;
}


