import { h , Component} from "preact" ;
// log
import {burylog , pagetrans} from "../../utils/hybrid/index" ;
import disablelog from "../../burylog/worklist/index" ;
import CryptoJS from "crypto-js" ;
const deliverySouce = '61' ;

export default class WorkItem extends Component {
	constructor(props){
		super(props);
		this.state = {};
		// console.log(this.ascCryptoFun('26A2CDED7A5F7750EFA04BF7AE693E21'));
	}
	/*
		intro : run phone call
	*/
	runPhoneCall(ev){
		if(ev.target){
			ev.stopPropagation() ;
			// ev.nativeEvent.stopImmediatePropagation() ;
			ev.cancelBubble = true ; 
			// log
			let {list = {}} = disablelog ;
			burylog(list.tel.actiontype , list.tel.pagetype) ;
		}
	}
	/*
		intro : run work apply
	*/
	runApply(ev){
		if(ev.target){
			ev.stopPropagation() ;
			// ev.nativeEvent.stopImmediatePropagation() ;
			ev.cancelBubble = true ;
			let {work = {}} = this.props ; 
			let {infoID = ''} = work ;
			// log
			let {list = {}} = disablelog ;
			burylog(list.apply.actiontype , list.apply.pagetype) ;
			try{
				window.delivery_new && window.delivery_new({
					infoId :  infoID, deliverySouce , 
					// displayGuideBox : true , // 
					deliveryCallBackFn : () => {
						// callback
						this.props.onApply && this.props.onApply(this.props.work.infoID) ;
					}
				}) ;
			}catch(err){console.log(err);}
		}
	}
	/*
		intro : asc crypto function
	*/
	ascCryptoFun(cipherText = ''){
		let secretKey = CryptoJS.enc.Base64.parse('ZGIyMTM5NTYxYzlmZTA2OA==') ;
		var cipherTextHexStr = CryptoJS.enc.Hex.parse(cipherText);
		var cipherTextBase64Str = CryptoJS.enc.Base64.stringify(cipherTextHexStr);
		console.log(cipherTextHexStr) ;
		return  CryptoJS.AES.decrypt(cipherTextBase64Str, secretKey , {
      iv: secretKey ,
      padding: CryptoJS.pad.Pkcs7,
      mode: CryptoJS.mode.CBC
    }) ;
	}
	/*
		intro : enter work info
	*/
	enterWorkInfo(ev){
		if(ev.target){
			// stop bubble
			ev.stopPropagation() ;
			// ev.nativeEvent.stopImmediatePropagation() ;
			ev.cancelBubble = true ; 

			// log
			let {list = {}} = disablelog ;
			burylog(list.list.actiontype , list.list.pagetype) ;

			// pagetrans
			let { work = {}} = this.props ;
			let {action = '' , content = {} , tradeline = 'job'} = work.action || {} ;
			pagetrans(tradeline , content) ;
		}
	}
	/*
		intro : render
	*/
	render(){
		let { work = {}} = this.props ;
		//1:  0
		let {
			quyu = '' , qyname = '' , infoID = 0 , title = '' , 
			jobname = '' , xinzi = '' , dateShow = '' , bottomTags ,
			phone = 0 , welfare = [] , isApply = 1
		} = work ;
		let fontNum = 0 ;
		// TODO 
		// phone = this.ascCryptoFun(phone) ;
		return (
			<div className="t-disable-work-item" onClick={this.enterWorkInfo.bind(this)}>
				<div className="t-row flex-between">
					<span>
						<span className="t-main-title">{ title }</span>
						<small className="t-content-font marginL8">
							<img  alt="ding" className="t-work-ding" src="data:image/svg+xml;base64,"/>{ jobname }
						</small>
					</span>
					<span>{dateShow}</span>
				</div>
				<div className="t-row flex-between t-work-apply">
					<div  className="flex1">
						<p className="t-main-color t-work-salary">{ xinzi }</p>
						<p className="nowrap-ellipsis t-work-welfare">
							{
								welfare 
								&& welfare !== "" 
								&& welfare.split(",").map((item , index) => {fontNum+= item.length; return index < 4 && fontNum  <= 12  && <i className="t-tag-on marginR8 t-welfare-tag" key={ index }>{item}</i>})
							}
						</p>
					</div>
					<p>
						<a href={"tel:" + phone}>
							<span 
									className="t-primary-button-off marginR8" 
									data-phone={phone} 
									onClick={this.runPhoneCall.bind(this)}></span>
						</a>
						{
							isApply * 1 === 1 
							? <span 
							data-type="disable"
							className="t-primary-button-on disable"
							onClick={this.runApply.bind(this)}></span>
							: <span 
							className="t-primary-button-on"
							onClick={this.runApply.bind(this)}></span>
						}
					</p>
				</div>
				<div className="t-row flex-between t-work-position">
					{
						bottomTags ? <i className="t-tag-off marginR8" style={{color : `${bottomTags.tagColor}` , borderColor : `${bottomTags.tagColor}`}}>{ bottomTags.tagTitle || "" }</i> : ""
					}
					<span className='t-content-font nowrap-ellipsis t-work-position-name'>{ qyname }</span>
					<span className='t-content-font'>{ quyu }</span>
				</div>
			</div>
		)
	}
}


